import React, { useEffect, useRef, useState } from 'react';
import {
  Image,
  Keyboard,
  Platform,
  ScrollView,
  TouchableNativeFeedback,
  View,
} from 'react-native';
import { styles } from './styles';
import { useRoute } from '@react-navigation/native';
import { BaseContainer, Button, HeaderBase, Text } from 'tradewize-component';
import { NavigationService } from '../../services/NavigationService';
import { useTranslation } from 'react-i18next';
import { verify_otp_icon } from '../../assets/images';
import { OtpInput, OtpInputRef } from 'react-native-otp-entry';
import { KeyboardAvoidingView } from 'react-native-keyboard-controller';
import colors from '../../theme/colors';
import {
  globalStyles,
  keyboardBehavior,
  keyboardOffset,
} from '../../global/styles';
import { handleVerifyOtpForgotPassword } from '../../helper/verifiOtp';
import { useCountdown } from '../../hooks/useCountDown';
import { useDispatch, useSelector } from 'react-redux';
import {
  resendCodeOtpAsync,
  resendCodeOtpReset,
  verifyEmailOtpReset,
} from '../../redux/features/verifyOtp/actions';
import { RootState } from '../../redux/store/types';

interface VerifyOtpParams {
  email: string;
  phone: string;
}

const VerifyOtp = () => {
  const { t } = useTranslation();
  const route = useRoute()?.params as VerifyOtpParams;
  const dispatch = useDispatch();

  const otpInputRef = useRef<OtpInputRef>(null);

  const [otp, setOtp] = useState<string>('');
  const [isResendCode, setIsResendCode] = useState(false);
  const resendOtpState = useSelector(
    (state: RootState) => state.verifyOtp.resendCode,
  );
  const verifyOtpEmailState = useSelector(
    (state: RootState) => state.verifyOtp.email,
  );

  const { formatted, start, reset } = useCountdown(59 * 1000, () => {
    setIsResendCode(true);
  });

  useEffect(() => {
    start();
  }, [start]);

  useEffect(() => {
    if (!resendOtpState.loading && resendOtpState.success && isResendCode) {
      dispatch(resendCodeOtpReset());
      reset();
      setIsResendCode(false);
      otpInputRef?.current?.clear();
      otpInputRef?.current?.blur();
    }
  }, [
    resendOtpState.loading,
    reset,
    resendOtpState.success,
    isResendCode,
    dispatch,
  ]);

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        styleButtonLeft={globalStyles.styleButtonBack}
        onPressBack={() => {
          NavigationService.goBack();
          dispatch(resendCodeOtpReset());
          dispatch(verifyEmailOtpReset());
        }}
        title={t('VERIFY_OTP')}
      />
      <KeyboardAvoidingView
        behavior={keyboardBehavior}
        keyboardVerticalOffset={keyboardOffset}
        style={styles.keyboardAvoidingView}
      >
        <TouchableNativeFeedback onPress={() => Keyboard.dismiss()}>
          <View style={styles.content}>
            <ScrollView
              keyboardDismissMode="interactive"
              contentContainerStyle={globalStyles.flex}
            >
              <Image
                source={verify_otp_icon}
                style={styles.verifyOtpIcon}
                resizeMode="contain"
              />

              <Text style={styles.title}>{t('VERIFY_CODE_DESC')}</Text>
              <Text style={styles.emailOrPhone}>
                {route.email || route.phone}
              </Text>

              <View style={styles.containerOtp}>
                <OtpInput
                  ref={otpInputRef}
                  numberOfDigits={6}
                  onTextChange={(code: string) => {
                    if (code?.length >= 6) {
                      otpInputRef?.current?.blur();

                      setTimeout(() => {
                        handleVerifyOtpForgotPassword({
                          email: route.email || '',
                          otp: code,
                        });
                      }, 150);
                    }
                    setOtp(code);
                  }}
                  onFocus={() => {
                    dispatch(verifyEmailOtpReset());
                  }}
                  theme={{
                    pinCodeContainerStyle: {
                      ...styles.pinCodeContainer,
                      borderColor: verifyOtpEmailState?.error
                        ? colors.alertColorError
                        : colors.borderGray,
                    },
                  }}
                  focusStickBlinkingDuration={500}
                  type="numeric"
                  textProps={{
                    accessibilityRole: 'text',
                    accessibilityLabel: 'OTP digit',
                    allowFontScaling: false,
                  }}
                  textInputProps={{
                    keyboardType: 'numeric',
                    autoComplete:
                      Platform.OS === 'android' ? 'sms-otp' : 'one-time-code',
                    textContentType: 'oneTimeCode',
                  }}
                  focusColor={colors.mainColorPrimary}
                  autoFocus={false}
                />
              </View>

              <View style={styles.expiredCodeContainer}>
                {!isResendCode && (
                  <Text style={styles.expiredCodeText}>
                    {t('VERIFY_CODE_EXPIRED_IN_NUMBER', { number: formatted })}
                  </Text>
                )}
              </View>

              <Text style={styles.resendCodeText}>
                {t('DID_NOT_RECEIVE_CODE')}
              </Text>
              <Button
                disabled={!isResendCode}
                variant="ghost"
                style={styles.resendCodeButton}
                onPress={() => {
                  if (isResendCode) {
                    dispatch(resendCodeOtpAsync(route.email));
                  }
                }}
              >
                <Text
                  style={[
                    styles.resendButtonText,
                    !isResendCode && styles.resendCodeButtonDisabled,
                  ]}
                >
                  {t('RESEND_CODE', { type: t('OTP') })}
                </Text>
              </Button>
            </ScrollView>
            <Button
              onPress={() =>
                handleVerifyOtpForgotPassword({ email: route.email, otp: otp })
              }
              fullWidth
              size="large"
              variant="primary"
              style={styles.buttonVerify}
            >
              <Text style={styles.buttonVerifyText}>{t('VERIFY')}</Text>
            </Button>
          </View>
        </TouchableNativeFeedback>
      </KeyboardAvoidingView>
    </BaseContainer>
  );
};

export default VerifyOtp;
