import i18n from '../i18n';
import { hideAlertAsync, showAlertAsync } from '../redux/features/app/actions';
import { verifyEmailOtpAsync } from '../redux/features/verifyOtp/actions';
import { store } from '../redux/store';
import { logger } from '../utils';

export const handleVerifyOtpForgotPassword = (payload: {
  email: string;
  otp: string;
}) => {
  try {
    const { email, otp } = payload || {};

    if (!email) {
      store.dispatch(
        showAlertAsync({
          visible: true,
          title: i18n.t('WARNING'),
          message: i18n.t('EMAIL_CANNOT_BE_EMPTY'),
          type: 'warning',
          buttonText: i18n.t('CLOSE'),
          onClose: () => {
            store.dispatch(hideAlertAsync());
          },
        }),
      );
      return;
    }

    if (!otp || otp?.length < 6) {
      store.dispatch(
        showAlertAsync({
          visible: true,
          title: i18n.t('WARNING'),
          message: i18n.t('OTP_MUST_BE_NUMBER_DIGITS', { number: 6 }),
          type: 'warning',
          buttonText: i18n.t('CLOSE'),
          onClose: () => {
            store.dispatch(hideAlertAsync());
          },
        }),
      );
      return;
    }

    store.dispatch(
      verifyEmailOtpAsync({ email: email.trim(), otpCode: otp.trim() }),
    );
  } catch (error) {
    logger('error handleVerifyOtpForgotPassword', error);
  }
};
