import {
  CHANGE_PASSWORD_ASYNC,
  CHANGE_PASSWORD_FAILURE,
  CHANGE_PASSWORD_PROGRESS,
  CHANGE_PASSWORD_RESET,
  CHANGE_PASSWORD_SUCCESS,
} from './contants';
import { ChangePasswordPayload } from './types';

// action saga
export const changePasswordAsync = (payload: ChangePasswordPayload) =>
  ({ type: CHANGE_PASSWORD_ASYNC, payload } as const);

// action reducer
export const changePasswordSuccess = () =>
  ({ type: CHANGE_PASSWORD_SUCCESS } as const);
export const changePasswordFailure = (payload: string) =>
  ({ type: CHANGE_PASSWORD_FAILURE, payload } as const);
export const changePasswordReset = () =>
  ({ type: CHANGE_PASSWORD_RESET } as const);
export const changePasswordProgress = (payload: boolean) =>
  ({ type: CHANGE_PASSWORD_PROGRESS, payload } as const);

export type ChangePasswordAction =
  | ReturnType<typeof changePasswordAsync>
  | ReturnType<typeof changePasswordSuccess>
  | ReturnType<typeof changePasswordFailure>
  | ReturnType<typeof changePasswordReset>
  | ReturnType<typeof changePasswordProgress>;
