import { Keyboard } from 'react-native';
import { changePasswordAsync } from '../redux/features/changePassword/actions';
import { store } from '../redux/store';
import { logger } from '../utils';

export interface FormChangePassword {
  newPassword: string;
  confirmPassword: string;
}

export interface ChangePasswordParams {
  email: string;
  otpCode: string;
}

export const handleSubmitChangePassword = (
  values: FormChangePassword,
  route: ChangePasswordParams,
) => {
  try {
    Keyboard.dismiss();
    store.dispatch(
      changePasswordAsync({
        ...route,
        newPassword: values.newPassword,
      }),
    );
  } catch (error) {
    logger('error handleSubmitChangePassword', error);
  }
};
