import React from 'react';
import { FlatList, Image, View } from 'react-native';
import { BaseContainer, Button, Text } from 'tradewize-component';
import { styles } from './styles';
import colors from '../../theme/colors';
import {
  default_jobs,
  home_guest_banner,
  user_icon,
} from '../../assets/images';
import { useTranslation } from 'react-i18next';
import listJobs from './jobs.json';
import { SvgXml } from 'react-native-svg';
import { IconClock, IconLocation } from '../../assets/svg';
import { formatCurrency, formatMiles } from '../../utils';
import { FORMAT_TIME_CLIENT_JOB_START, SCREENS_ENUM } from '../../types/global';
import dayjs from 'dayjs';
import { navigationRef } from '../../services/NavigationService';

const HomeGuest = () => {
  const { t } = useTranslation();

  const renderItem = ({ item }: { item: any }) => {
    return (
      <View style={styles.jobItemContainer}>
        <Text style={styles.jobItemTitle}>
          {item.activityName} - {item.companyRoleName}
        </Text>
        <View style={styles.jobItemInfoContainer}>
          <Image source={default_jobs} style={styles.jobItemInfoImage} />
          <View style={styles.jobItemInfoTextContainer}>
            <Text style={styles.jobItemInfoText}>{`${item?.state ?? ''}, ${
              item?.street ?? ''
            }, ${item?.city ?? ''}`}</Text>
            <View style={styles.jobItemInfoTextDistanceContainer}>
              {item.houseDistance != null && (
                <View style={styles.jobItemInfoTextDistanceItemContainer}>
                  <SvgXml xml={IconLocation()} width={16} height={16} />
                  <Text style={styles.miles}>
                    {formatMiles(item.houseDistance)} miles
                  </Text>
                </View>
              )}

              {/* Start / End */}
              <View style={styles.estStartTextContainer}>
                <SvgXml xml={IconClock()} width={16} height={16} />
                <Text style={styles.estStartText}>
                  {t('EST_START', {
                    date: `${
                      item?.startDate
                        ? dayjs(item?.startDate).format(
                            FORMAT_TIME_CLIENT_JOB_START,
                          )
                        : ''
                    }`,
                  })}
                </Text>
              </View>
            </View>
          </View>
        </View>

        <View style={styles.jobItemDescriptionContainer}>
          <Text style={styles.jobItemDescriptionText}>
            {t('AMOUNT', {
              amount: formatCurrency(item.price),
            })}
          </Text>
        </View>
      </View>
    );
  };

  return (
    <BaseContainer
      statusBarStyle="light-content"
      backgroundStatusBarColor={colors.mainColorPrimary}
      style={styles.container}
      backgroundColor={colors.white}
    >
      <View style={styles.headerContainer}>
        <Button
          onPress={() => navigationRef.goBack()}
          variant="ghost"
          style={styles.userIconContainer}
        >
          <Image source={user_icon} style={styles.userIcon} />
          <Text style={styles.loginToContinueText}>
            {t('LOGIN_TO_CONTINUE')}
          </Text>
        </Button>
      </View>

      <View style={styles.contentContainer}>
        <Image source={home_guest_banner} style={styles.homeGuestBanner} />

        <Text style={styles.homeGuestText}>{t('RECENT_FEATURED_JOBS')}</Text>

        <FlatList
          contentContainerStyle={styles.listJobsContainer}
          data={listJobs}
          renderItem={renderItem}
          keyExtractor={item => item.id}
        />
      </View>

      <View style={styles.footerContainer}>
        <Button
          style={[styles.footerButton, styles.footerButtonSecondary]}
          variant="primary"
          title={t('SIGN_UP')}
          textStyle={styles.footerButtonText}
          onPress={() => navigationRef.navigate(SCREENS_ENUM.RegisterNavigator)}
        />
        <Button
          style={[
            styles.footerButton,
            { backgroundColor: colors.mainColorPrimary },
          ]}
          textStyle={[styles.footerButtonText, { color: colors.white }]}
          variant="secondary"
          title={t('LOGIN')}
          onPress={() => navigationRef.goBack()}
        />
      </View>
    </BaseContainer>
  );
};

export default HomeGuest;
