import React, { useMemo, useRef, useState } from 'react';
import { styles } from './styles';
import {
  BaseContainer,
  Button,
  HeaderBase,
  Input,
  Text,
} from 'tradewize-component';
import { useTranslation } from 'react-i18next';
import { Formik, FormikProps } from 'formik';
import { Keyboard, TouchableNativeFeedback, View } from 'react-native';
import colors from '../../theme/colors';
import {
  globalStyles,
  keyboardBehavior,
  keyboardOffset,
} from '../../global/styles';
import { SvgXml } from 'react-native-svg';
import { EyeIconOff } from '../../assets/svg';
import { EyeIcon } from '../../assets/svg';
import { KeyboardAvoidingView } from 'react-native-keyboard-controller';
import { getChangePasswordSchema } from '../../validators/changePassword';
import {
  ChangePasswordParams,
  FormChangePassword,
  handleSubmitChangePassword,
} from '../../helper/changePassword';
import { NavigationService } from '../../services/NavigationService';
import { useRoute } from '@react-navigation/native';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../redux/store/rootReducer';
import { changePasswordReset } from '../../redux/features/changePassword/actions';

const initialValues: FormChangePassword = {
  newPassword: '',
  confirmPassword: '',
};

const ChangePassword = () => {
  const route = useRoute()?.params as ChangePasswordParams;
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const { error } = useSelector((state: RootState) => state.changePassword);

  const formRef = useRef<FormikProps<FormChangePassword>>(null);

  const [secureText, setSecureText] = useState<string[]>(['']);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const validationSchema = useMemo(() => getChangePasswordSchema(), [t]);

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        styleButtonLeft={globalStyles.styleButtonBack}
        onPressBack={() => {
          NavigationService.goBack();
          dispatch(changePasswordReset());
        }}
      />
      <Text style={styles.title}>{t('CREATE_NEW_A_PASSWORD')}</Text>
      <Text style={styles.description}>{t('ENTER_NEW_YOUR_PASSWORD')}</Text>

      <KeyboardAvoidingView
        behavior={keyboardBehavior}
        style={styles.keyboardAvoidingView}
        keyboardVerticalOffset={keyboardOffset}
      >
        <TouchableNativeFeedback onPress={() => Keyboard.dismiss()}>
          <View style={globalStyles.flex}>
            <Formik<FormChangePassword>
              initialValues={initialValues}
              onSubmit={values => handleSubmitChangePassword(values, route)}
              innerRef={formRef}
              validationSchema={validationSchema}
            >
              {({ values, errors, touched, handleChange, setTouched }) => (
                <View style={styles.formContainer}>
                  {['newPassword', 'confirmPassword'].map(field => (
                    <View key={field}>
                      <Input
                        maxLength={50}
                        secureTextEntry={!secureText?.includes(field)}
                        containerStyle={styles.containerInput}
                        labelStyle={globalStyles.labelInput}
                        isError={
                          !!(
                            errors[field as keyof FormChangePassword] &&
                            touched[field as keyof FormChangePassword]
                          )
                        }
                        label={
                          field === 'newPassword'
                            ? t('NEW_PASSWORD')
                            : t('CONFIRM_PASSWORD')
                        }
                        placeholder={
                          field === 'newPassword'
                            ? t('ENTER_NEW_YOUR_PASSWORD')
                            : t('ENTER_YOUR_CONFIRM_PASSWORD')
                        }
                        inputContainerStyle={styles.inputContainer}
                        borderColorActive={colors.mainColorPrimary}
                        borderColor={colors.additionalSecondary}
                        placeholderTextColor={colors.grayScale60}
                        value={values[field as keyof FormChangePassword]}
                        onChangeText={handleChange(field)}
                        inputStyle={styles.input}
                        onFocus={() => {
                          setTouched({ ...touched, [field]: false });
                        }}
                        onBlur={() => {
                          setTouched({ ...touched, [field]: true });
                        }}
                        fullWidth={true}
                        rightIcon={
                          <Button
                            variant="ghost"
                            onPress={() =>
                              setSecureText(prev =>
                                prev.includes(field)
                                  ? prev.filter(item => item !== field)
                                  : [...prev, field],
                              )
                            }
                          >
                            <SvgXml
                              xml={
                                secureText?.includes(field)
                                  ? EyeIcon()
                                  : EyeIconOff()
                              }
                              width={24}
                              height={24}
                            />
                          </Button>
                        }
                      />

                      {errors[field as keyof FormChangePassword] &&
                        touched[field as keyof FormChangePassword] && (
                          <Text style={globalStyles.errorText}>
                            {errors[field as keyof FormChangePassword]}
                          </Text>
                        )}
                    </View>
                  ))}
                  {error && (
                    <View style={globalStyles.errorBackground}>
                      <Text style={globalStyles.errorText}>{error}</Text>
                    </View>
                  )}
                </View>
              )}
            </Formik>

            <Button
              onPress={() => formRef.current?.handleSubmit()}
              size="large"
              style={styles.buttonSubmit}
            >
              <Text style={styles.buttonSubmitText}>{t('CONFIRM')}</Text>
            </Button>
          </View>
        </TouchableNativeFeedback>
      </KeyboardAvoidingView>
    </BaseContainer>
  );
};

export default ChangePassword;
