import {
  FORGOT_PASSWORD_ASYNC,
  FORGOT_PASSWORD_FAILED,
  FORGOT_PASSWORD_SUCCESS,
  FORGOT_PASSWORD_RESET,
} from './contants';

// action saga
export const forgotPasswordAsync = (payload: string) =>
  ({ type: FORGOT_PASSWORD_ASYNC, payload } as const);

// action reducer
export const forgotPasswordSuccess = () =>
  ({ type: FORGOT_PASSWORD_SUCCESS } as const);
export const forgotPasswordFailed = (payload: string) =>
  ({ type: FORGOT_PASSWORD_FAILED, payload } as const);
export const forgotPasswordReset = () =>
  ({ type: FORGOT_PASSWORD_RESET } as const);

export type ForgotPasswordAction = ReturnType<
  | typeof forgotPasswordAsync
  | typeof forgotPasswordSuccess
  | typeof forgotPasswordFailed
  | typeof forgotPasswordReset
>;
