import { Keyboard } from 'react-native';
import { forgotPasswordAsync } from '../redux/features/forgotPassword/actions';
import { store } from '../redux/store';
import { logger } from '../utils';

export interface FormForgotPassword {
  email: string;
}

export const handleSubmit = (values: FormForgotPassword) => {
  try {
    Keyboard.dismiss();
    const { email } = values;
    if (!email) {
      return;
    }
    setTimeout(() => {
      store.dispatch(forgotPasswordAsync(email));
    }, 150);
  } catch (error) {
    logger('error handleSubmit', error);
  }
};
