import {
  VERIFY_EMAIL_OTP_ASYNC,
  VERIFY_EMAIL_OTP_FAILED,
  VERIFY_EMAIL_OTP_SUCCESS,
  RESEND_CODE_OTP_ASYNC,
  RESEND_CODE_OTP_SUCCESS,
  RESEND_CODE_OTP_FAILED,
  RESEND_CODE_OTP_PROGRESS,
  RESEND_CODE_OTP_RESET,
  VERIFY_EMAIL_OTP_RESET,
} from './contants';
import { VerifyEmailOtpPayload } from './types';

// action saga
export const verifyEmailOtpAsync = (payload: VerifyEmailOtpPayload) =>
  ({ type: VERIFY_EMAIL_OTP_ASYNC, payload } as const);
export const resendCodeOtpAsync = (payload: string) =>
  ({ type: RESEND_CODE_OTP_ASYNC, payload } as const);

// action reducer
export const verifyEmailOtpSuccess = () =>
  ({ type: VERIFY_EMAIL_OTP_SUCCESS } as const);
export const verifyEmailOtpFailed = (payload: string) =>
  ({ type: VERIFY_EMAIL_OTP_FAILED, payload } as const);
export const resendCodeOtpSuccess = () =>
  ({ type: RESEND_CODE_OTP_SUCCESS } as const);
export const resendCodeOtpFailed = (payload: string) =>
  ({ type: RESEND_CODE_OTP_FAILED, payload } as const);
export const resendCodeOtpProgress = (payload: boolean) =>
  ({ type: RESEND_CODE_OTP_PROGRESS, payload } as const);
export const resendCodeOtpReset = () =>
  ({ type: RESEND_CODE_OTP_RESET } as const);
export const verifyEmailOtpReset = () =>
  ({ type: VERIFY_EMAIL_OTP_RESET } as const);

export type VerifyOtpAction = ReturnType<
  | typeof verifyEmailOtpAsync
  | typeof verifyEmailOtpSuccess
  | typeof verifyEmailOtpFailed
  | typeof resendCodeOtpAsync
  | typeof resendCodeOtpSuccess
  | typeof resendCodeOtpFailed
  | typeof resendCodeOtpProgress
  | typeof resendCodeOtpReset
  | typeof verifyEmailOtpReset
>;
