import React, { useMemo, useRef } from 'react';
import { styles } from './styles';
import {
  BaseContainer,
  Button,
  HeaderBase,
  Input,
  Text,
} from 'tradewize-component';
import { useTranslation } from 'react-i18next';
import { NavigationService } from '../../services/NavigationService';
import { Keyboard, TouchableNativeFeedback, View } from 'react-native';
import colors from '../../theme/colors';
import { KeyboardAvoidingView } from 'react-native-keyboard-controller';
import { Formik, FormikProps } from 'formik';
import {
  globalStyles,
  keyboardBehavior,
  keyboardOffset,
} from '../../global/styles';
import { getForgotPasswordSchema } from '../../validators/forgotPassword';
import { FormForgotPassword, handleSubmit } from '../../helper/forgotPassword';
import { RootState } from '../../redux/store/types';
import { useDispatch, useSelector } from 'react-redux';
import { forgotPasswordReset } from '../../redux/features/forgotPassword/actions';

const ForgotPassword = () => {
  const { t } = useTranslation();
  const formRef = useRef<FormikProps<{ email: string }>>(null);
  const dispatch = useDispatch();
  const initialValues: FormForgotPassword = {
    email: '',
  };

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const validationSchema = useMemo(() => getForgotPasswordSchema(), [t]);

  const { error } = useSelector((state: RootState) => state.forgotPassword);

  return (
    <BaseContainer
      backgroundStatusBarColor={colors.white}
      style={styles.container}
    >
      <HeaderBase
        styleButtonLeft={globalStyles.styleButtonBack}
        onPressBack={() => {
          NavigationService.goBack();
          dispatch(forgotPasswordReset());
        }}
      />

      <KeyboardAvoidingView
        behavior={keyboardBehavior}
        style={styles.keyboardAvoidingView}
        keyboardVerticalOffset={keyboardOffset}
      >
        <TouchableNativeFeedback onPress={() => Keyboard.dismiss()}>
          <View style={styles.content}>
            <Text style={styles.title}>{t('FORGOT_PASSWORD')}</Text>
            <Text style={styles.description}>
              {t('FORGOT_PASSWORD_DESCRIPTION')}
            </Text>

            <Formik
              initialValues={initialValues}
              onSubmit={handleSubmit}
              innerRef={formRef}
              validationSchema={validationSchema}
            >
              {({ values, errors, touched, handleChange, setTouched }) => (
                <View style={globalStyles.flex}>
                  {(['email'] as (keyof FormForgotPassword)[]).map(field => {
                    return (
                      <View style={styles.containerFormik} key={field}>
                        <Input
                          fullWidth={true}
                          containerStyle={styles.containerInput}
                          placeholder={t('ENTER_YOUR_EMAIL')}
                          label={t('EMAIL')}
                          inputStyle={styles.input}
                          labelStyle={globalStyles.labelInput}
                          inputContainerStyle={styles.inputContainer}
                          isError={
                            !!(
                              errors[field as keyof FormForgotPassword] &&
                              touched[field as keyof FormForgotPassword]
                            )
                          }
                          borderColorActive={colors.mainColorPrimary}
                          borderColor={colors.additionalSecondary}
                          placeholderTextColor={colors.grayScale60}
                          onChangeText={text => {
                            handleChange(field)(text.trim());
                          }}
                          onFocus={() => {
                            setTouched({ ...touched, [field]: false });
                          }}
                          onBlur={() => {
                            setTouched({ ...touched, [field]: true });
                          }}
                          value={values.email}
                        />
                        {errors[field] && touched[field] && (
                          <Text style={globalStyles.errorText}>
                            {errors[field]}
                          </Text>
                        )}

                        {error && (
                          <Text style={globalStyles.errorBackground}>
                            {error}
                          </Text>
                        )}
                      </View>
                    );
                  })}
                </View>
              )}
            </Formik>
            <Button
              onPress={() => formRef.current?.handleSubmit()}
              size="large"
              style={styles.button}
            >
              <Text style={styles.buttonText}>{t('NEXT')}</Text>
            </Button>
          </View>
        </TouchableNativeFeedback>
      </KeyboardAvoidingView>
    </BaseContainer>
  );
};

export default ForgotPassword;
