import i18next from 'i18next';
import * as Yup from 'yup';

export const getChangePasswordSchema = () =>
  Yup.object({
    newPassword: Yup.string()
      .min(
        8,
        i18next.t('PASSWORD_MUST_BE_AT_LEAST_NUMBER_CHARACTERS_LONG', {
          number: 8,
        }),
      )
      .matches(
        /[^A-Za-z0-9]/,
        i18next.t('PASSWORD_MUST_CONTAIN_AT_LEAST_ONE_SPECIAL_CHARACTER'),
      )
      .test(
        'password-strength',
        i18next.t('PASSWORD_MUST_CONTAIN_AT_LEAST'),
        value => {
          if (!value) return false;
          const conditions = [
            /[A-Z]/.test(value), // có chữ in hoa
            /[a-z]/.test(value), // có chữ thường
            /[0-9]/.test(value), // có số
            /[^A-Za-z0-9]/.test(value), // có ký tự đặc biệt
          ];
          // Đếm số điều kiện đúng
          const passedCount = conditions.filter(Boolean).length;
          return passedCount >= 3;
        },
      )
      .required(i18next.t('PASSWORD_CANNOT_BE_EMPTY')),
    confirmPassword: Yup.string()
      .required(i18next.t('PASSWORD_CANNOT_BE_EMPTY'))
      .test(
        'passwords-match',
        i18next.t('PASSWORDS_DO_NOT_MATCH'),
        function (value) {
          return value === this.parent.newPassword;
        },
      ),
  });
